.header{

  &__flex-wrapper{
  display: flex;
  align-items: center;
  width: 60%;
  justify-content: space-between;
  }

  &__content{
  background-color: #27262A;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: space-between;
  padding: 29px 10px 30px;
  }

  &__btn{
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 19px;
  background-color: #8d81ac;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  line-height: 15px;
  width: 140px;
  margin-right: 80px;
  }

  &__logo:hover .logo-logo-img{
  background-color: white;
  }

  &__logo:hover .logo-text {
  color: white;
  }

  &__btn:hover {
  background-color: white;
  color: #8d81ac;
  }
}

.logo {
  display: flex;
  align-items: center;
  margin-left: 80px;

  &-logo-img {
    display: inline-block;
    width: 43px;
    height: 41px;
    -webkit-mask: url("../img/Main-logo.svg") no-repeat;
    mask: url("../img/Main-logo.svg") no-repeat;
    background-color: #8d81ac;
  }

  &-text {
    color: #8d81ac;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    margin-left: 17px;
    text-decoration: none;
  }
}

.main__menu {
  display: flex;
  justify-content: space-between;

  &__item a {
  color: #8d81ac;
  font-size: 16px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
  }
  &__item{
  margin-right: 48px;
  }
}
.about__forkio{

  &__content{
    text-align: center;
    padding-top: 60px;
    background: url("../img/Header-layer.png") no-repeat center;
    background-size: cover;
    width: 1200px;
    height: 700px;
  }

  &__title{
      color: #ffffff;
      font-size: 82px;
      font-weight: 700;
      line-height: 1em;
    }

  &__slogan{
    margin-top: 18px;
    color: #8d81ac;
    font-size: 30px;
    font-weight: 400;
    line-height: 1em;
  }
  &__version{
    margin-top: 18px;
    color: #848e97;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
  }
}
.forkio-statistics__content{
  padding-top: 129px;
  padding-bottom: 58px;
  text-align: center;
}
.forkio-statistics{

  &__heading{
    color: #635c73;
    font-size: 54px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 40px;
  }
  &__description{
    margin-top: 39px;
    color: #323232;
    font-size: 19px;
    font-weight: 400;
    line-height: 30px;
    margin-bottom: 36px;
  }
}

.download-btn{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -199px;
}

.download-btn__title{
  display: inline-block;
  width: 390px;
  padding-top: 23px;
  padding-bottom: 23px;
  background-color: #8d81ac;
  border-radius: 50px;
  text-decoration: none;
  color: #ffffff;
  font-size: 21px;
  font-weight: 700;
  line-height: 16px;

  &:hover{
    background-color: white;
    color: #8d81ac;
  }
}
.download-btn__info{
  margin-top: 19px;
  color: #848e97;
  font-size: 14px;
  font-weight: 400;
}

.statistic-links{
  width: 365px;
  display: inline-flex;
  justify-content: space-between;

  &__container{
  padding: 3px 10px;
  font-size: 12px;
  color: #24292e;
  background: #eff3f6 linear-gradient(-180deg, #fafbfc 0%, #eff3f6 90%);
  border: 1px solid rgba(27, 31, 35, 0.2);
  float: left;
  text-decoration: none;
  vertical-align: middle;
  border-radius: 0.25em 0px 0px 0.25em;
  line-height: 20px;
  font-weight: 900;
  }
  &__container:hover{
    background: #eff3f6 linear-gradient(-180deg, #fafbfc 0%, #eff3f6 0%);
    border: 1px solid rgba(27, 31, 35, 0.3);
  }
  &__count{
    padding: 3px 10px;
    font-size: 12px;
    font-weight: 600;
    line-height: 20px;
    color: #24292e;
    vertical-align: middle;
    float: left;
    font-weight: 600;
    line-height: 20px;
    background-color: #fff;
    border: 1px solid rgba(27, 31, 35, 0.2);
    border-left: 0;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    text-decoration: none;
  }
}

.fas{
  width: 16px;
  height: 16px;
  vertical-align: middle;
  margin-right: 2px;
}